.pad-wrap {
  position: relative;
  width: 100%;
  height: 100%;
  border: solid 1px #cdcdcd;
  box-sizing: border-box;
  user-select: none;
  overflow: hidden; }

.pad-hide {
  display: none; }

.pad-wrap .split-page-wrap {
  position: absolute;
  right: 5px;
  bottom: 0;
  z-index: 100; }

.pad-wrap .no {
  color: #cdcdcd !important;
  cursor: default; }

.pad-wrap .no:hover {
  text-decoration: none !important; }

.pad-wrap .split-page-wrap a {
  text-decoration: none;
  color: #444; }

.pad-wrap .split-page-wrap span {
  margin-left: 15px;
  display: inline-block; }

.pad-wrap .split-page-wrap .go-page-btn {
  margin-left: 10px; }

.pad-wrap .split-page-wrap a:hover {
  text-decoration: underline; }

.pad-wrap .split-page-wrap .page-number-input {
  width: 30px;
  text-align: center;
  outline: none;
  margin: 0;
  padding: 0; }

.pad-wrap ul,
.pad-wrap li {
  list-style: none;
  padding: 0;
  margin: 0; }

.pad-wrap .toolbar-wrap {
  position: absolute;
  width: 100%;
  height: 38px;
  z-index: 10; }

.pad-wrap .toolbar-wrap .toolbar-list {
  width: 100%;
  height: 100%;
  background: #ececec; }

.pad-wrap .toolbar-wrap .toolbar-list .toolbar-item {
  float: left;
  width: 40px;
  text-align: center;
  line-height: 36px;
  box-sizing: border-box;
  margin-left: 3px;
  cursor: pointer; }

.pad-wrap .toolbar-wrap .toolbar-list .toolbar-item .item-icon {
  font-size: 20px;
  display: inline-block;
  width: 100%;
  height: 38px;
  border-radius: 5px;
  box-sizing: border-box;
  border: solid 1px #cdcdcd; }

.pad-wrap .toolbar-wrap .toolbar-list .toolbar-item .child-toolbar-list:before {
  content: ".";
  width: 0;
  height: 0;
  clear: both;
  visibility: hidden; }

.pad-wrap .toolbar-wrap .toolbar-list .toolbar-item .child-toolbar-list {
  position: absolute;
  z-index: 100;
  margin-top: 3px;
  box-shadow: 0 0 2px 1px #cdcdcd;
  background: #fff;
  padding: 5px 2px;
  display: none; }

.pad-wrap .toolbar-wrap .toolbar-list .selected-item .child-toolbar-list {
  display: block; }

.pad-wrap .toolbar-wrap .toolbar-list .toolbar-item .item-icon:hover {
  background: #cdcdcd; }

.pad-wrap.disabled .toolbar-wrap .toolbar-list .toolbar-item .item-icon:hover {
  background: none; }

.pad-wrap.disabled .toolbar-wrap .toolbar-list .toolbar-item .item-icon {
  color: #cecece; }

.pad-wrap .toolbar-wrap .toolbar-list .toolbar-item .item-icon[active] {
  background: #cdcdcd;
  color: #fff; }

.pad-wrap.disabled .toolbar-wrap .toolbar-list .toolbar-item .item-icon[active] {
  background: none;
  color: #cecece; }

.pad-wrap.disabled .split-page-wrap {
  color: #cdcdcd; }

.pad-wrap.disabled .split-page-wrap a {
  color: #cdcdcd; }

.pad-wrap.disabled .split-page-wrap a:hover {
  text-decoration: none;
  cursor: default; }

.pad-wrap .toolbar-wrap .toolbar-list .toolbar-item:first-child {
  margin-left: 0; }

.pad-wrap .can-wrap-outer {
  position: absolute;
  height: calc(22%);
  height: calc(100% - 78px);
  top: 38px;
  width: 100%;
  overflow: hidden; }

.pad-wrap .can-wrap-outer .can-wrap {
  position: absolute;
  height: 100%;
  width: 100%;
  overflow: hidden; }

.pad-wrap .can-wrap canvas {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1; }

.pad-wrap .can-wrap canvas.buffer-can-1 {
  z-index: 9; }

.pad-wrap .can-wrap canvas.buffer-can-2 {
  z-index: 8; }

.pad-wrap .can-wrap canvas.buffer-can-3 {
  z-index: 7; }

.pad-wrap .tool-input {
  visibility: hidden;
  position: absolute;
  z-index: 0;
  width: 180px;
  height: 30px;
  border: solid 1px #cdcdcd;
  font-size: 18px; }

.pad-wrap .pad-tab-wrap {
  height: 40px;
  position: absolute;
  bottom: 0;
  width: 100%; }

.pad-wrap .pad-tab-wrap .pad-tab-list {
  width: 100%;
  height: 100%; }

.pad-wrap .pad-tab-wrap .pad-tab-list .pad-tab-item {
  height: 100%;
  padding: 0 30px;
  display: inline-block;
  line-height: 40px;
  background: #ececec;
  cursor: pointer;
  float: left;
  position: relative;
  margin-left: 2px; }

.pad-wrap .pad-tab-wrap .pad-tab-list .pad-tab-item.active {
  background: #cdcdcd; }

.pad-wrap .pad-tab-wrap .pad-tab-list .pad-tab-item:first-child {
  margin-left: 0; }

.pad-wrap .pad-tab-wrap .pad-tab-list .pad-tab-item .icon-close {
  position: absolute;
  top: 2px;
  line-height: normal;
  right: 2px;
  visibility: hidden; }

.pad-wrap .pad-tab-wrap .pad-tab-list .pad-tab-item:hover .icon-close {
  visibility: visible; }

.pad-wrap .pad-tab-wrap .pad-tab-list .pad-tab-item .icon-close:hover {
  color: red; }

.pad-wrap .tool-img {
  position: absolute;
  z-index: -1;
  visibility: hidden; }

.pad-wrap .scroll-y-wrap {
  position: absolute;
  right: 0;
  height: 100%;
  width: 10px;
  top: 0;
  z-index: 20; }

.pad-wrap .scroll-x-wrap {
  position: absolute;
  bottom: 0;
  left: 0;
  width: calc(80%);
  width: calc(100% - 10px);
  height: 10px;
  z-index: 20; }

.pad-wrap .scroll-toolbar {
  position: absolute;
  left: 0;
  top: 0;
  cursor: pointer;
  width: 100%;
  border-radius: 3px;
  box-shadow: 0 0 5px #666 inset; }

.pad-wrap .scroll-x-wrap .scroll-x {
  width: 200px;
  height: 100%; }

.pad-wrap .scroll-y-wrap .scroll-y {
  width: 100%;
  height: 200px; }
